div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  background-color: #ddd;
  margin: 0 auto;
  color: #222;
}

header {
  flex-basis: 30vh;
}

main {
  padding: 30px 0;
  display: flex;
  flex-grow: 1;
}

aside {
  min-width: 240px;
}

section.page {
  flex-grow: 1;
}

footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
}